import React, { useState } from 'react';
import './Quiz.css';

const quizData = [
  {
    question: "   ?",
    options: [
      "   ,    .",
      "   ,    .",
      "  ,  .",
      "     ."
    ],
    correctAnswer: 0
  },
  {
    question: "         ?",
    options: [
      "       ",
      "        ",
      "       ",
      "       "
    ],
    correctAnswer: 1
  },
  {
    question: "     ?",
    options: [
      "  ",
      "   ",
      "  ",
      "   "
    ],
    correctAnswer: 3
  },
  {
    question: "       ?",
    options: [
      "    ",
      "     ",
      "   ",
      "    "
    ],
    correctAnswer: 2
  },
  {
    question: "          ?",
    options: [
      "  ",
      "    ",
      "  ",
      "   "
    ],
    correctAnswer: 1
  }
];

function Quiz() {
  const [currentQuestion, setCurrentQuestion] = useState(0);
  const [score, setScore] = useState(0);
  const [showScore, setShowScore] = useState(false);

  const handleAnswerClick = (selectedAnswerIndex) => {
    if (selectedAnswerIndex === quizData[currentQuestion].correctAnswer) {
      setScore(score + 1);
    }

    const nextQuestion = currentQuestion + 1;
    if (nextQuestion < quizData.length) {
      setCurrentQuestion(nextQuestion);
    } else {
      setShowScore(true);
    }
  };

  const restartQuiz = () => {
    setCurrentQuestion(0);
    setScore(0);
    setShowScore(false);
  };

  return (
    <div className="quiz-container">
      {showScore ? (
        <div className="score-section">
           : {score} / {quizData.length}
          <button onClick={restartQuiz}>    </button>
        </div>
      ) : (
        <>
          <div className="question-section">
            <div className="question-count">
              <span> {currentQuestion + 1}</span>/{quizData.length}
            </div>
            <div className="question-text">{quizData[currentQuestion].question}</div>
          </div>
          <div className="answer-section">
            {quizData[currentQuestion].options.map((option, index) => (
              <button key={index} onClick={() => handleAnswerClick(index)}>
                {option}
              </button>
            ))}
          </div>
        </>
      )}
    </div>
  );
}

export default Quiz;
