import React, { useState } from 'react';
import './App.css';
import Quiz from './Quiz';
import Header from './Header';
import StartScreen from './StartScreen';
import { FullPageChat } from "flowise-embed-react";

function App() {
  const [showQuiz, setShowQuiz] = useState(false);
  const [started, setStarted] = useState(false);

  const newsArticles = [
    {
      title: "       ",
      summary: "          .",
      link: "#"
    },
    {
      title: "    ",
      summary: "  ,          .",
      link: "#"
    },
    {
      title: "  ,   ",
      summary: "            .",
      link: "#"
    },
    {
      title: "      ",
      summary: "        .",
      link: "#"
    },
    {
      title: "    ,  ",
      summary: "           .",
      link: "#"
    },
    {
      title: "   ,    ",
      summary: "            .",
      link: "#"
    },
    {
      title: "    (VR)  ",
      summary: "           .",
      link: "#"
    },
    {
      title: "     ",
      summary: "           .",
      link: "#"
    },
    {
      title: "     ",
      summary: "            .",
      link: "#"
    },
    {
      title: "     ",
      summary: "            .",
      link: "#"
    }
  ];

  const handleStart = () => {
    setStarted(true);
  };

  const handleHomeClick = () => {
    setShowQuiz(false);
  };

  if (!started) {
    return <StartScreen onStart={handleStart} />;
  }

  return (
    <div className="App">
      <Header 
        showQuizButton={!showQuiz} 
        onQuizClick={() => setShowQuiz(true)} 
        onHomeClick={handleHomeClick}
      />
      <main className="App-main">
        {showQuiz ? (
          <Quiz />
        ) : (
          <>
            <section className="chatbot-section">
              <div className="chat-container">
                <FullPageChat
                  chatflowid="bf97e04d-7f6e-4500-b490-e4ee644f9526"
                  apiHost="https://flow.edutrack.kr"
                  theme={{
                    chatWindow: {
                      width: '100%',
                      height: '100%',
                      containerWidth: '100%',
                      containerHeight: '100%',
                    }
                  }}
                />
              </div>
            </section>
            <section className="news-section">
              <h2> </h2>
              <div className="article-container">
                <div className="article-scroll">
                  {newsArticles.concat(newsArticles).map((article, index) => (
                    <article key={index} className="news-article">
                      <h3>{article.title}</h3>
                      <p>{article.summary}</p>
                      <a href={article.link} target="_blank" rel="noopener noreferrer"> </a>
                    </article>
                  ))}
                </div>
              </div>
            </section>
          </>
        )}
      </main>
      <footer className="App-footer">
        <p> 2023    . All rights reserved.</p>
      </footer>
    </div>
  );
}

export default App;
